.track {
  margin-bottom: 110px;
}

.track__container {
  position: relative;
  display: flex;
  justify-content: flex-end;
  padding: 78px 0;
  background-color: #F3F3F5;
  border-radius: 14px;
  overflow: hidden;
}

.track__letter-bg {
  position: absolute;
  top: 61px;
  left: -108px;
  width: 773px;
  height: 773px;
  background: #66AC27;
  border-radius: 50%;
}

.track__letter {
  position: absolute;
  top: 60px;
  left: 138px;
}

.track__content {
  margin-right: 156px;
}

.track__title {
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
  font-size: 32px;
  line-height: 65px;
  color: #000;
}

.track__sub-title {
  margin-bottom: 30px;
  font-family: OpenSans, serif;
  font-size: 18px;
  line-height: 28px;
  color: #101010;
}

.track__trace-form {
  margin-bottom: 42px;
}

.track__mobile {
  max-width: 491px;
  margin-bottom: 20px;
  font-family: OpenSans, serif;
  font-size: 16px;
  line-height: 26px;
  color: #101010;
  opacity: 0.5;
}

.track__mobile-btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 182px;
  height: 52px;
  background-color: #F9F9FA;
  border: 1px solid #000000;
  border-radius: 4px;
}

.track__mobile-btn:first-child {
  margin-right: 21px;
}