.navigation {
  padding: 16px 0;
  background-color: #fff;
  border-bottom: 1px solid #EBEBEB;
}

.navigation__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.navigation__logo {
  margin-right: 40px;
}

.navigation__container-flex {
  display: flex;
  align-items: center;
}

.navigation__links {
  display: flex;
  margin-right: 40px;
}

.navigation__links a {
  padding: 12px 20px;
  margin-right: 26px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #000;
  text-decoration: none;
  border-radius: 5px;
  transition: 0.2s ease color, 0.3s ease background-color;
}

.navigation__links a:hover {
  color: #fff;
  background-color: #66AC27;
  transition: 0.2s ease color, 0.3s ease background-color;
}

.navigation__links a:last-child {
  margin-right: 0;
}

.navigation__lc {
  display: inline-flex;
  align-items: center;
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
  line-height: 24px;
  color: #66AC27;
  text-decoration: none;
}

.navigation__lc svg {
  margin-right: 16px;
}
