.trace {
    display: flex;
    align-items: center;
}

.trace__input {
    width: 352px;
    padding: 12px 20px;
    margin-right: 20px;
    font-family: OpenSans, serif;
    font-size: 16px;
    line-height: 14px;
    color: #000;
    background: #F8F9FA;
    border: 1px solid #BECAE2;
    border-radius: 5px;
}

.trace__input::placeholder {
    font-family: OpenSans, serif;
    font-size: 16px;
    line-height: 14px;
    color: #ABB7C7;
}

.trace__btn {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 213px;
    height: 48px;
    font-family: OpenSans, serif;
    font-size: 16px;
    line-height: 30px;
    color: #66AC27;
    background: transparent;
    border: 1.5px solid #66AC27;
    border-radius: 5px;
    cursor: pointer;
}

.trace__loupe-svg {
    margin-right: 10px;
}
