.footer {
  padding-top: 80px;
  background-color: #fff;
}

.footer__container {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  margin-bottom: 69px;
}

.footer__nav-links {
  display: flex;
}

.footer__nav-links div:first-child {
  margin-right: 177px;
}

.footer__nav-links li {
  margin-bottom: 25px;
}

.footer__nav-links li:last-child {
  margin-bottom: 0;
}

.footer__link {
  display: block;
  min-width: 350px;
  font-family: 'Montserrat', sans-serif;
  font-size: 24px;
  font-weight: 300;
  line-height: 29px;
  text-decoration: none;
  color: #1B1B1F;
}

.footer__link:hover {
  font-weight: 400;
  color: #66AC27;
  transition: 0.3s ease color;
}

.footer__mobile {
  font-family: 'Montserrat', sans-serif;
  font-size: 26px;
  line-height: 31px;
  color: #1B1B1F;
}

.footer__mobile-desc {
  margin-bottom: 22px;
  font-family: OpenSans, sans-serif;
  font-weight: 300;
  font-size: 15px;
  line-height: 20px;
  color: #7B7B7D;
}

.footer__mail-contacts {
  margin-bottom: 18px;
}

.footer__mail-contacts:last-child {
  margin-bottom: 0;
}

.footer__mail-title {
  margin-bottom: 6px;
  font-family: OpenSans, sans-serif;
  font-weight: 300;
  font-size: 13px;
  line-height: 18px;
  color: #1B1B1F;
}

.footer__mail {
  font-family: 'Montserrat', sans-serif;
  font-size: 18px;
  line-height: 22px;
  color: #1B1B1F;
}

.footer__copyright {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 22px 0 30px 0;
  border-top: 1px solid #EBEBEB;
}

.footer__copyright-text {
  font-family: OpenSans, sans-serif;
  font-size: 14px;
  line-height: 20px;
  color: #000000;
  opacity: 0.6;
}
