.services {
  padding-top: 83px;
}

.services__item-container {
  display: flex;
}

.services__item {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  flex: 1;
  height: 440px;
  padding-left: 60px;
  position: relative;
  overflow: hidden;
  text-decoration: none;
}

.services__item_padding-1 {
  padding-bottom: 123px;
}

.services__item_padding-2 {
  padding-bottom: 117px;  
}

.services__item_padding-3 {
  padding-bottom: 142px;
}

.services__item-bg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
}

.services__item-title {
  max-width: 310px;
  margin-bottom: 10px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
  font-size: 30px;
  line-height: 38px;
  letter-spacing: -0.01em;
  color: #fff;
}

.services__item-title_width {
  max-width: 301px;
}

.services__item-sub-title {
  max-width: 294px;
  margin-bottom: 28px;
  font-family: OpenSans, serif;
  font-size: 18px;
  line-height: 26px;
  color: #fff;
}

.services__item-hidden {
  max-height: 0;
  overflow: hidden;
  transition: 0.5s ease max-height;
}

.services__item:hover .services__item-hidden{
  max-height: 126px;
  transition: 0.6s ease max-height;
}
