.advantages {
  padding-top: 130px;
  padding-bottom: 110px;
  background-color: #fff;
}

.advantages__title {
  margin-bottom: 90px;
  font-family: 'Montserrat', sans-serif;
  font-size: 40px;
  font-weight: 600;
  line-height: 65px;
  color: #000;
  text-align: center;
}

.advantages__container {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.advantages__item {
  display: flex;
  align-items: center;
  padding: 37px;
  flex: 26%;
  margin-right: 20px;
  margin-bottom: 40px;
  background-color: #F8F9FB;
  border-radius: 14px;
}

.advantages__item:nth-last-child(-n+3) {
  margin-bottom: 0;
}

.advantages__item:nth-child(3n) {
  margin-right: 0;
}

.advantages__circle {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  width: 59px;
  height: 59px;
  margin-right: 22px;
  border-radius: 50%;
  background-color: #66AC27;
}

.advantages__item-title {
  max-width: 324px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 500;
  font-size: 21px;
  line-height: 27px;
  color: #000;
}
