.header {
  padding-top: 108px;
}

.header__title {
  max-width: 802px;
  margin-bottom: 18px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
  font-size: 55px;
  line-height: 65px;
  letter-spacing: -0.01em;
  color: #101010;
}

.header__title span {
  color: #66AC27;
}

.header__sub-title {
  margin-bottom: 45px;
  font-family: OpenSans, serif;
  font-size: 20px;
  line-height: 28px;
  color: #101010;
}

.header__btn {
  width: 281px;
  height: 48px;
  font-family: OpenSans, serif;
  font-size: 18px;
  line-height: 30px;
  color: #fff;
  background-color: #66AC27;
  border: none;
  border-radius: 5px;
}