.partners {
  padding-top: 100px;
  padding-bottom: 106px;
  background: #F8F9FB;
}

.partners__title {
  margin-bottom: 65px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
  font-size: 40px;
  line-height: 65px;
  color: #000;
}

.partners__container {
  display: flex;
  flex-wrap: wrap;
}

.partners__item {
  display: flex;
  align-items: center;
  flex: 20%;
}

.partners__item:nth-child(-n+5) {
  margin-bottom: 69px;
}

.partners__item:nth-child(3),
.partners__item:nth-child(4),
.partners__item:nth-child(8),
.partners__item:nth-child(9){
  justify-content: center;
}

.partners__item:nth-child(5n) {
  justify-content: flex-end;
}